<template>
  <Fieldset class="mb-6">
    <template #label>
      <span class="text-grey-03">Shipping Label</span>
    </template>
    <template #default>
      <div>
        <ShippingCarrier :carriers="carriers" :selected="selectedCarrier" @select="handleSelectCarrier" />
      </div>
    </template>
  </Fieldset>
  <Fieldset class="mb-6">
    <template #label>
      <span class="text-grey-03">Buyback Details</span>
    </template>
    <template #default>
      <div class="p-4">
        <Input v-model="data.box_id" label="Buyback ID" :view="validation.box_id.$invalid && 'danger' || 'default'" />
        <small class="text-red-base block" v-for="(error, errorIndex) of validation.box_id.$errors" :key="errorIndex">{{ error.$message }}. </small>
      </div>
    </template>
  </Fieldset>
  <Fieldset>
    <template #label>
      <span class="text-grey-03">Contact Information</span>
    </template>
    <template #default>
      <div class="grid grid-cols-2 p-4">
        <div class="col-span-1 grid grid-cols-2 gap-6">
          <div>
            <Input v-model="data.shipping_firstname" label="First Name" block :view="validation.shipping_firstname.$invalid && 'danger' || 'default'" />
            <small class="text-red-base block" v-for="(error, errorIndex) of validation.shipping_firstname.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
          <div>
            <Input v-model="data.shipping_lastname" label="Last Name" block :view="validation.shipping_lastname.$invalid && 'danger' || 'default'" />
            <small class="text-red-base block" v-for="(error, errorIndex) of validation.shipping_lastname.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
          <div class="col-span-2">
            <Input v-model="data.customer_email" label="Email Address" block :view="validation.customer_email.$invalid && 'danger' || 'default'"  />
            <small class="text-red-base block" v-for="(error, errorIndex) of validation.customer_email.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
          <div class="col-span-2">
            <Input v-model="data.shipping_address_text" label="Street Address" block icon icon-block v-gmap="address" :view="validation.valid.$invalid && 'danger' || 'default'">
              <template #icon>
                <i class="material-icons text-lg">location_on</i>
              </template>
            </Input>
            <small class="text-red-base block" v-for="(error, errorIndex) of validation.valid.$errors" :key="errorIndex">{{ error.$message }}. </small>
            <small>Street address, P.O box, company name, c/o.</small>
          </div>
          <div class="col-span-2">
            <Input v-model="data.shipping_suite" block />
            <small>Apartment, suite, building, floor, etc.</small>
          </div>
          <div>
            <!-- <Input v-model="data.shipping_phone" label="Phone" block icon icon-block :view="validation.shipping_phone.$invalid && 'danger' || 'default'">
              <template #icon>
                <i class="material-icons text-lg">phone</i>
              </template>
            </Input> -->
            <InputPhone ref="refShippingPhone" v-model="data.shipping_phone" label="Phone" block country-autofill-setting />
            <small class="text-red-base block" v-for="(error, errorIndex) of validation.shipping_phone.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
        </div>
      </div>
    </template>
  </Fieldset>
</template>

<script>
import { defineComponent, reactive, ref, watch} from 'vue'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import CustomerForm from 'components/Customer/Form.vue'
import Breadcrumb from 'bbUi/components/breadcrumb/breadcrumb.vue'
import Input from 'bbUi/components/input/input.vue'
import InputPhone from 'components/InputPhone.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import { useVuelidate } from '@vuelidate/core'
import { required, validAddress, email } from '@/validations/index'
import { helpers, requiredIf } from '@vuelidate/validators'
import Select from 'bbUi/components/select/select.vue'
import ShippingCarrier from 'components/ShippingCarrier.vue'

export default defineComponent({
  components: {
    Button,
    Card,
    CustomerForm,
    Breadcrumb,
    Input,
    Fieldset,
    InputPhone,
    Select,
    ShippingCarrier
},
  props: {
    data: {
      type: Object,
      default: () => ({
        box_id: null,
        shipping_address1: null,
        shipping_city: null,
        shipping_firstname: null,
        shipping_lastname: null,
        shipping_phone: null,
        shipping_state: null,
        shipping_suite: '',
        shipping_zip: null,
        shipping_country: null,
        shipping_address_text: null,
        // shipping_company_name: null,
        type: null,
        advance_label: null,
        customer_email: null
      })
    },
    carriers: {
      type: Array,
      default: () => ([])
    },
    advanceShippingLabels: {
      type: Array,
      default: () => ([])
    },
    selectedCarrier: {
      type: String,
      default: ''
    }
  },
  emits: ['update:data', 'select-carrier'],
  setup(props, {emit}) {
    const carriers = ref(props.carriers)
    const selectedCarrier = ref(props.selectedCarrier)
    const selectedAdvanceShippingLabel = ref(null)
    const advanceShippingLabels = ref(props.advanceShippingLabels)
    const data = reactive(props.data)
    const refShippingPhone = ref(null)

    Object.assign(data,{
      valid: false
    })
    const rules = {
      valid: {
        sameAs: validAddress
      },
      // shipping_company_name: {
      //   required
      // },
      box_id: {
        required
      },
      shipping_firstname: {
        required
      },
      shipping_lastname: {
        required
      },
      shipping_phone: {
        required,
        invalidNumber: helpers.withMessage('Phone number is invalid', () => !data.shipping_phone ? true : refShippingPhone.value?.isValid)
      },
      advance_label: {
        required: requiredIf(() => selectedCarrier.value == 'usps'),
      },
      customer_email: {
        required: helpers.withMessage('Email address is required', required),
        email: helpers.withMessage('Invalid email address', email)
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    watch(() => ({
      data,
      selectedCarrier,
      advance_label: props.data.advance_label
    }), () =>{
      emit('update:data', Object.assign(data, {
        type: selectedCarrier.value != 'usps' ? selectedCarrier.value : props.data.advance_label
      }))
    }, {
      deep: true,
      immediate: true
    })

    const selectAdvanceShippingLabel = (data) => {
      selectedAdvanceShippingLabel.value = data
      props.data.advance_label = data.value
    };
    const handleSelectCarrier = (carrierId=null) => {
      if(!carrierId) return
      selectedCarrier.value = carrierId
    }
    
    return {
      refShippingPhone,
      carriers,
      selectedCarrier,
      address: {
        valid: 'valid',
        country: 'shipping_country',
        route: 'shipping_address1',
        locality: 'shipping_city',
        administrative_area_level_1: 'shipping_state',
        formatted_address: 'shipping_address_text',
        postal_code: 'shipping_zip',
        disallowCustomAddress: true,
        subpremise: 'shipping_suite'
      },
      validation,
      validate,
      advanceShippingLabels,
      selectedAdvanceShippingLabel,
      selectAdvanceShippingLabel,
      handleSelectCarrier
    }
  }
})
</script>

<style scoped>
  .selectable-card {
    margin-bottom: 0 !important;
  }
  .required-custom-select {
    border-color: #f65955 !important;
  }
  .required-label {
    color: #f64844 !important;
  }
</style>