<template>
  <div>
    <Breadcrumb
      class="mb-8 mt-2"
      pageTitle="Add Shipping"
      :backTo="{ name: 'layoutShipping' }"
      :items="breadcrumb">
      <template #right>
        <Button color="teal" @click="generateShippingLabel" :loading="loading">Generate Shipping Label</Button>
      </template>
    </Breadcrumb>
    <Card>
      <Form
        ref="refForm"
        v-model:data="data"
        :carriers="shippingCarriers"
        :advanceShippingLabels="advanceShippingLabels"
        :selected-carrier="selectedCarrier"
      />
    </Card>
  </div>
  <Modal size="xs" v-model="isShowModal" hide-header hide-footer>
    <div class="text-center py-10">
      <img class="mb-10 mx-auto" src="@/assets/icon/success.svg" alt="Success Icon">
      <div class="mt-8 mb-10">
        <h5 class="mb-4">Success!</h5>
        <p>The new shipping label has been added.</p>
      </div>
      <Button view="smooth" color="teal" class="mr-3" link :to="{name: 'shipping'}">Continue</Button>
    </div>
  </Modal>
</template>

<script>
import { computed, defineComponent, ref} from 'vue'
import { useCreate, useLabels, useShippingCarrier } from 'composable/shipping'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import CustomerForm from 'components/Customer/Form.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Input from 'bbUi/components/input/input.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Form from 'components/Shipping/Form.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import { useRouter } from 'vue-router'

export default defineComponent({
  components: {
    Button,
    Card,
    CustomerForm,
    Breadcrumb,
    Input,
    Fieldset,
    Form,
    Modal
  },
  async setup() {
    const { data, action: actionCreate, loading } = useCreate()
    const { state: stateCouriers, stateAdvanceShippingLabel, action: actionLabels } = useLabels()
    const { shippingCarriers, selectedCarrier } = useShippingCarrier()
    const refForm = ref(null)
    const isShowModal = ref(false)
    const router = useRouter()
    await actionLabels()
    const isEmptyCourier = stateCouriers.value.length === 0
    if(isEmptyCourier) return router.push({
        name: 'shipping'
    })
    // [TODO] It should be added to the backend, so we don't have to filter it.
    const availableCouriers = [{
      id: 'usps',
      name: 'USPS',
      icon: '/icon/shipping/usps.svg'
    }, {
      id: 'fedex',
      name: 'FedEx',
      icon: '/icon/shipping/fedex.svg'
    }, {
      id: 'ups',
      name: 'UPS',
      icon: '/icon/shipping/ups.svg'
    }]
    const advanceShippingLabels = computed(() => stateAdvanceShippingLabel.value)
    const couriers = computed(() => availableCouriers.filter(courier => stateCouriers.value.map(label => label.toLowerCase()).includes(courier.id))) // filter courier ( availableCourier ) based on stateCourier
    const selectedCourier = ref(availableCouriers[0].id)
    const breadcrumb = [{
      text: 'Shipping',
      to: {
        name: 'layoutShipping'
      }
    }, {
      text: 'Add Shipping Label'
    }]
    const generateShippingLabel = async() => {
      const isValid = refForm.value.validate()
      if( isValid ) await actionCreate(() => {
        isShowModal.value = true
      })
    }
    return {
      data,
      loading,
      refForm,
      availableCouriers,
      couriers,
      breadcrumb,
      selectedCourier,
      isShowModal,
      generateShippingLabel,
      advanceShippingLabels,
      shippingCarriers,
      selectedCarrier
    }
  }
})
</script>
<style lang="scss" scoped>
.border-secondary-shipping-method {
  border-color: #dbdbdb;
}
.input__icon {
  background-color: white;
}
</style>
